.card{
    border: 1px solid rgb(20, 196, 240);

    display:flex;
    width: 350px;
    margin: 20px 60px;
    text-align: center;
    border-radius: 20px;
    padding: 5px;
    box-shadow: 4px 4px 4px 4px rgb(68, 68, 68);
    text-align: center;
    
}

img{
    display:flex;
    width: 6rem;
    padding: 6px;
    box-shadow: 2px 2px 2px 2px rgba(44, 43, 43, 0.5);
    margin: 10px;
    border-radius: 50px;
    
    
}